<template>
  <div class="checkbox-view">
    <el-checkbox
        v-model="checked"
        @change="checkTap"
        >选我</el-checkbox
    >
  </div>
</template>
<script>
export default {
  data() {
    return {
      checked: true,
    };
  },
  methods: {
    checkTap(val) {
        console.log("我改变了!",val);
    },
  },
};
</script>
<style scoped>
/*  
  样式穿透（后两种需要scss支持）
  >>>  
  :deep()  
  /deep/    
*/
.checkbox-view {
    display: flex;
    padding: 30px;
}

/* checkbox */
.checkbox-view >>> .el-checkbox__input.is-checked .el-checkbox__inner {
  background-color: #f50537;
  border-color: #f50537;
}
.checkbox-view >>> .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #f50537;
  border-color: #f50537;
}
.checkbox-view >>> .el-checkbox__inner {
  width: 24px;
  height: 24px;
  border-radius: 2px;
  font-size: 18px;
  background: #fff;
  border-color: #dcdfe6;
}

/* label样式 */
:deep() .el-checkbox__label ,
:deep() .el-checkbox__input.is-checked+.el-checkbox__label {
    color: #1A1A1A;
}

/* focus border-color */
:deep() .el-checkbox__input.is-focus .el-checkbox__inner {
  border-color: #f50537;
}

/* 勾选状态：对勾样式 */
:deep()  .el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #fff;
  border-color: #fff;
}
:deep()  .el-checkbox__inner::after {
  border: 2px solid #fff;
  border-left: 0px;
  border-top: 0px;
  width: 5px;
  height: 9px;
  left: 8px;
  position: absolute;
  top: 4px;
}
/* checkbox hover */
:deep()  .el-checkbox__inner:hover {
  border-color: #f50537;
}

</style>
